:root {
  --primary: #008DD1;
  --secondary: #43A58B;
  --info: #B0CB14;
  --text: #0C0B0B;
  --text-90: rgba(12, 11, 11, 0.9);
  --success: #0A1B28;
  --warning: #050F17;
  --card: #FAFAFA;
  --white: #FFFFFF;
  --black: #0C0B0B;
  --background: #FFFFFF;
  --card-background: #FAFAFA;
  --error: #dc3545;
  --gray: #999999;
}

:root {
  --font-family: Poppins, sans-serif, Arial, sans-serif;
  --font-base: Poppins, sans-serif;
  --font-fallback: Arial, sans-serif;
  --font-weight-regular: 400;
  --font-weight-medium: 500;
  --font-weight-semibold: 600;
  --font-weight-bold: 700;
  --font-size-body4: 0.75rem;
  --font-size-body3: 0.875rem;
  --font-size-body2: 1rem;
  --font-size-body1: 1.125rem;
  --font-size-body: 1.25rem;
  --font-size-title1: 1.375rem;
  --font-size-title: 1.5rem;
  --font-size-h6: 1.75rem;
  --font-size-h5: 1.875rem;
  --font-size-h4: 2.5rem;
  --font-size-h3: 3.125rem;
  --font-size-h2: 3.75rem;
  --font-size-h1: 6.25rem;
  --line-height-tight: 1;
  --line-height-normal: 1;
  --line-height-relaxed: 1.4;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 16px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
  font-family: var(--font-family, Poppins, sans-serif, Arial, sans-serif);
  line-height: 1;
  color: var(--black);
  background-color: transparent;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

a {
  text-decoration: none;
  color: inherit;
}

ul,
ol {
  list-style: none;
}

button {
  border: none;
  background: none;
  cursor: pointer;
  font-family: inherit;
}

input,
textarea,
select {
  font-family: inherit;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
  font-size: 16px;
}

body {
  font-family: Poppins, sans-serif, Arial, sans-serif;
  font-weight: 400;
  line-height: 1.4;
