  max-width: 500px;
}
@media (max-width: 1199px) {
  .navbar__search-form {
    max-width: 100%;
    width: 100%;
  }
}
.navbar__search-wrapper {
  position: relative;
  display: flex;
  align-items: center;
  height: 40px;
  padding: 10px 20px;
  border: 1px solid var(--white);
  border-radius: 8px;
  background: var(--white);
  box-sizing: border-box;
  gap: 10px;
}
.navbar__search-icon {
  flex-shrink: 0;
  color: var(--gray);
  font-size: 16px;
  width: 16px;
  height: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.navbar__search-text {
  font-family: var(--font-family, Poppins, sans-serif, Arial, sans-serif);
  font-size: 16px;
  font-weight: 500;
  color: var(--gray);
  line-height: normal;
  white-space: nowrap;
  pointer-events: none;
  user-select: none;
}
.navbar__search-input {
  width: 100%;
  padding: 10px 20px 10px 0px;
  background: transparent;
  border: none;
  border-radius: 8px;
  color: var(--gray);
  font-family: var(--font-family, Poppins, sans-serif, Arial, sans-serif);
  font-size: 16px;
  font-weight: 500;
  transition: 0.3s ease;
}
.navbar__search-input:focus {
  outline: none;
}
.navbar__search-input::placeholder {
  color: var(--gray);
  font-weight: 500;
  font-size: 16px;
}
.navbar__search-input::-webkit-search-cancel-button, .navbar__search-input::-webkit-search-decoration {
  -webkit-appearance: none;
  appearance: none;
  display: none;
}
.navbar__search-input::-ms-clear, .navbar__search-input::-ms-reveal {
  display: none;
  width: 0;
  height: 0;
}
.navbar__search-input::-moz-search-clear {
  display: none;
}
.navbar__search-clear {
  position: absolute;
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
  background: transparent;
  border: none;
  padding: 0;
  width: 20px;
  height: 20px;
  display: none;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  color: var(--gray);
  opacity: 1;
  transition: 0.3s ease;
  flex-shrink: 0;
  z-index: 10;
}
.navbar__search-clear i {
  font-size: 14px;
  line-height: 1;
}
.navbar__search-clear:hover {
  opacity: 1;
  color: var(--gray);
}
.navbar__search-clear:focus {
  outline: none;
}
@supports selector(:has(*)) {
  .navbar__search-wrapper:has(.navbar__search-input:not(:placeholder-shown)) .navbar__search-clear {
    display: flex;
  }
}

